import React, { Suspense } from "react";
import SideBarMenu from "../../commonComponents/SideBarMenu/SideBarMenu";
import "./Bom.css";
import BomNavBar from "../../commonComponents/Bom/BomNavBar/BomNavBar";
import { useSelector } from "react-redux";
import { setBomMappingTempData, setBomMappingTempData2 } from "../../redux-toolkit/slice/action";
const MaterialMap = React.lazy(() => import("../../commonComponents/Material/MaterialMap/MaterialMap"));
const BomGet = React.lazy(() => import("../../commonComponents/Bom/BomGet/BomGet"));
const BomUpdate = React.lazy(() => import("../../commonComponents/Bom/BomUpdate/BomUpdate"));
const Loading = React.lazy(() => import("../../commonComponents/Loading/Loading"));
const BomCreate = React.lazy(() => import("../../commonComponents/Bom/BomCreate/BomCreate.js/BomCreate"));
const BomFlatBom = React.lazy(() => import("../../commonComponents/Bom/BomFlatBom/BomFlatBom"));
const BomHierarchicalBom = React.lazy(() => import("../../commonComponents/Bom/BomHierarchicalBom/BomHierarchicalBom"));
function Bom() {
  const { bomAction, uploadLoader, bomMappingDataTemp } = useSelector((state) => state.action);
  // const {} = useSelector((state) => state.taskpane);
  return (
    <>
      <div className="commonContainer" id="commonContainer">
        <div className="commonNavBar">
          <BomNavBar />
        </div>
        <div className="commonBody ">
          <div className="commonSidebarBox">
            <SideBarMenu />
          </div>
          <div className="commonActivityArea">
            {bomAction === "map" ? (
              <Suspense fallback={<div></div>}>
                <MaterialMap
                  mappingConfig={bomMappingDataTemp}
                  setmappingConfig={setBomMappingTempData}
                  setmappingConfig2={setBomMappingTempData2}
                />{" "}
              </Suspense>
            ) : null}
            {bomAction === "getRegularBom" ? (
              <Suspense fallback={<div></div>}>
                <BomGet />
              </Suspense>
            ) : null}
            {bomAction === "getFlatBom" ? (
              <Suspense fallback={<div></div>}>
                {" "}
                <BomFlatBom />{" "}
              </Suspense>
            ) : null}
            {bomAction === "getHierarchicalBom" ? (
              <Suspense fallback={<div></div>}>
                {" "}
                <BomHierarchicalBom />{" "}
              </Suspense>
            ) : null}
            {bomAction === "create" ? (
              <Suspense fallback={<div></div>}>
                <BomCreate />{" "}
              </Suspense>
            ) : null}
            {bomAction === "update" ? (
              <Suspense fallback={<div></div>}>
                <BomUpdate />{" "}
              </Suspense>
            ) : null}
          </div>
        </div>
      </div>
      {uploadLoader && (
        <Suspense fallback={<div></div>}>
          <Loading />
        </Suspense>
      )}
    </>
  );
}

export default Bom;
