import React from "react";
import { useDispatch, useSelector } from "react-redux";
import "./BomNavBar.css";
import { BASE_URL } from "../../../../config";
import { setBomAction } from "../../../redux-toolkit/slice/action";

function BomNavBar() {
  const { nextPage } = useSelector((state) => state?.taskpane);
  const dispatch = useDispatch();
  const { bomAction } = useSelector((state) => state?.action);
  const opengetMenu = () => {
    document.getElementById("bom-get-menu-box").style.display = "block";
  };
  const closeMenuBox = () => {
    document.getElementById("bom-get-menu-box").style.display = "none";
  };
  return (
    <div className="navbar">
      <div className="navbar-logo">
        <img src={`${BASE_URL}/assets/beacon-without-background -logo.png `} />
      </div>
      {nextPage === "bom" && (
        <div className="navbar-option">
          <span
            className={`navbar-button ${bomAction == "map" ? "selectedAction" : null}`}
            onClick={() => dispatch(setBomAction("map"))}
          >
            Map
          </span>
          <span
            className={`navbar-button bom-get-menu-button ${
              bomAction == "getRegularBom" || bomAction == "getFlatBom" || bomAction == "getHierarchicalBom"
                ? "selectedAction"
                : null
            }`}
            onClick={opengetMenu}
          >
            Get
            <ul className="bom-get-menu-box" id="bom-get-menu-box" onMouseLeave={closeMenuBox}>
              <li
                onClick={() => {
                  closeMenuBox();
                  dispatch(setBomAction("getRegularBom"));
                }}
              >
                Regular BOM{" "}
              </li>
              <li
                onClick={() => {
                  closeMenuBox();
                  dispatch(setBomAction("getFlatBom"));
                }}
              >
                Flat BOM
              </li>
              <li
                onClick={() => {
                  closeMenuBox();
                  dispatch(setBomAction("getHierarchicalBom"));
                }}
              >
                Hierarchical BOM
              </li>
            </ul>
          </span>
          <span
            className={`navbar-button ${bomAction == "create" ? "selectedAction" : null}`}
            onClick={() => dispatch(setBomAction("create"))}
          >
            Create
          </span>
          <span
            className={`navbar-button ${bomAction == "update" ? "selectedAction" : null}`}
            onClick={() => dispatch(setBomAction("update"))}
          >
            Update
          </span>
        </div>
      )}
    </div>
  );
}

export default BomNavBar;
